/**
 * @time 2018年7月25日17:20:43
 * @author joke<277637411@qq.com>
 * @description 图表基类-添加公共方法
 */
import global from '../config';
import UUID from 'uuid-js';
import * as d3 from 'd3';

class Base {
	constructor({
		chart,
		axis,
		legend,
		tooltip
	}, rootConfig) {
		this._init({
			chart,
			axis,
			legend,
			tooltip
		}, rootConfig);
	}

	_init({
		chart,
		axis,
		legend,
		tooltip
	}, rootConfig) {
		this.chart = chart;
		this.axis = axis;
		this.legend = legend;
		this.tooltip = tooltip;
		this.rootConfig = rootConfig;
		this.styleConfig = global;
		this.aColors = (this.chart.colors || []).concat(this._copyArr(this.styleConfig.aColors));
		this.colors = (this.chart.colors || []).concat(this._copyArr(this.styleConfig.colors));
		this.default = this._deepCopyObj({
			chart,
			axis,
			legend,
			tooltip
		});

		// chart selected params default enable 'single' module ;
		// single or multiple or false(Boolean) ;
		if ('enableChartSelect' in this.chart) {
			this.selectedModule = this.chart.enableChartSelect ? (this.chart.selectedModule || 'single') : false;
		} else {
			this.selectedModule = this.rootConfig.enableChartSelect ? (this.chart.selectedModule || 'single') : false;
		}
		this.selectedData = [];

		this.layout = {
			padding: this.styleConfig.padding,
			title: this.chart.title && this.chart.title.length ? this.styleConfig.titleHeight : 0,
			x: this.styleConfig.xTitleHeight,
			y: this.styleConfig.yTitleWidth, // title
			xLabel: 0, // label
			yLabel: 0,
			x0: 0,
			y0: 0,
			x0Axis: 0,
			y0Axis: 0,
			legend: {
				list: [], // 计算后的图例列表
				totalWidth: 0, // 图例所占容器的总宽度
				widthArr: [], // 图例极值存放没一列最长的图例长度
				columnLegendCount: 0 // 一列所放的图例个数
			},
			area: { // 画图区域
				w: 0,
				h: 0
			},
			w: 0,
			h: 0
		};
	}

	// 计算布局信息
	_computedLayout() {
		var $el = document.querySelector(this.chart.el);
		var styles = $el.currentStyle ? $el.currentStyle : window.getComputedStyle($el, null);

		this.width = this.chart.width || +styles.width.replace('px', '');
		this.height = this.chart.height || +styles.height.replace('px', '');

		this.layout.w = this.width;
		this.layout.h = this.height;
		// 找出xlabel 值最长的一项 算出长度
		// 旋转算出高度即为xLabel高度
		// 根据配置项整理所有的布局数据
		// 不是组合图
		if (typeof this.chart.type === 'string') {
			var key = 'x',
				value = 'y';

			if (this.chart.custom instanceof Array && this.chart.custom.length && this.chart.custom[0]) key = this.chart.custom[0];
			if (this.chart.custom instanceof Array && this.chart.custom.length && this.chart.custom[1]) value = this.chart.custom[1];

			// 计算Y轴label的最大宽度
			if ('x' in this.axis || 'y' in this.axis) {
				// 有Y轴
				if ('y' in this.axis) {
					var yLabelArr = [];
					var yLabelArrDefault = [];
					if ('data' in this.axis.y && this.axis.y.data.length) {
						yLabelArrDefault = this.axis.y.data.concat();
					} else {
						yLabelArrDefault = this.chart.data.map(val => {
							return val[value];
						})
					}

					// 如果有自定义的formatter函数
					if (this.axis.y.formatter) {
						yLabelArr = yLabelArrDefault.map((val, index) => {
							return this.axis.y.formatter(val);
						})
					} else {
						yLabelArr = [...yLabelArrDefault];
					}

					yLabelArr.sort((n, m) => {
						return JSON.stringify(m).length - JSON.stringify(n).length;
					})

					var yLabelText = '' + yLabelArr[0];
					yLabelText = yLabelText.length > this.styleConfig.textMaxLength ? yLabelText.substring(0, this.styleConfig.textMaxLength + 1) + '...' : yLabelText;
					let oSvgy = d3.select('body').append('svg');
					let texty = oSvgy.append('text').text(yLabelText);

					var yLabelWidth = (texty.nodes())[0].getBBox().width + 10;
					oSvgy.remove();
				}

				// 有X轴
				if ('x' in this.axis) {
					var xLabelArrDefault = [];
					var xLabelArr = [];
					if ('data' in this.axis.x && this.axis.x.data.length) {
						xLabelArrDefault = this.axis.x.data.concat();
					} else {
						xLabelArrDefault = this.chart.data.map(val => {
							return val[key];
						})
					}

					// 如果有自定义的formatter函数 需要转换字符后再计算布局
					if (this.axis.x.formatter) {
						xLabelArr = xLabelArrDefault.map((val, index) => {
							return this.axis.x.formatter(val);
						})
					} else {
						xLabelArr = [...xLabelArrDefault];
					}

					xLabelArr.sort((n, m) => {
						return JSON.stringify(m).length - JSON.stringify(n).length;
					})
					var xLabelText = '' + xLabelArr[0];
					xLabelText = xLabelText.length > this.styleConfig.textMaxLength ? xLabelText.substring(0, this.styleConfig.textMaxLength + 1) + '...' : xLabelText;
					let oSvg = d3.select('body').append('svg');
					let text = oSvg.append('text').text(xLabelText);
					let xLabelWidth = (text.nodes())[0].getBBox().width + 10;
					oSvg.remove();

					if ('rotate' in this.axis.x) {
						var tw = 0;
						// 旋转后的长度
						tw = xLabelWidth * Math.sin(this.axis.x.rotate * Math.PI / 180);
					}

					this.layout = {
						padding: this.styleConfig.padding,
						title: this.chart.title && this.chart.title.length ? this.styleConfig.titleHeight : 0,
						x: (this.axis.x && this.axis.x.title) ? this.styleConfig.xTitleHeight : 0,
						y: (this.axis.y && this.axis.y.title) ? this.styleConfig.yTitleWidth : 0,
						xLabel: tw ? tw : this.styleConfig.fontSize + 10,
						yLabel: yLabelWidth ? yLabelWidth : 0,
						legend: {
							list: [],
							totalWidth: 0, // 图例所占容器的总宽度
							widthArr: [],
							columnLegendCount: 0
						},
						area: {
							w: 0,
							h: 0
						},
						w: this.width,
						h: this.height
					};

					this.layout.area.w = this.width - this.layout.padding * 2 - this.layout.y - this.layout.legend.totalWidth - this.layout.yLabel;
					this.layout.area.h = this.height - this.layout.padding * 2 - this.layout.title - this.layout.x - this.layout.xLabel;
				}

			} else {
				// 没有坐标轴
				this.layout = {
					padding: this.styleConfig.padding,
					title: this.chart.title && this.chart.title.length ? this.styleConfig.titleHeight : 0,
					x: 0,
					y: 0,
					xLabel: 0,
					yLabel: 0,
					legend: {
						list: [],
						totalWidth: 0, // 图例所占容器的总宽度
						widthArr: [],
						columnLegendCount: 0
					},
					area: { // 画图区域
						w: 0,
						h: 0
					},
					w: this.width,
					h: this.height
				};
				this.layout.area.w = this.width - this.layout.padding * 2 - this.layout.y - this.layout.legend.totalWidth - this.layout.yLabel;
				this.layout.area.h = this.height - this.layout.padding * 2 - this.layout.title - this.layout.x - this.layout.xLabel;
			}
		} else if (this.chart.type instanceof Array && this.chart.type.length == 2) {
			// 组合图 限两个
			var key = 'x',
				value = 'y',
				key1 = 'x',
				value1 = 'y';

			if (this.chart.custom instanceof Array && this.chart.custom[0] && this.chart.custom[0][0]) key = this.chart.custom[0][0];
			if (this.chart.custom instanceof Array && this.chart.custom[0] && this.chart.custom[0][1]) value = this.chart.custom[0][1];
			if (this.chart.custom instanceof Array && this.chart.custom[1] && this.chart.custom[1][0]) key1 = this.chart.custom[1][0];
			if (this.chart.custom instanceof Array && this.chart.custom[1] && this.chart.custom[1][1]) value1 = this.chart.custom[1][1];

			if ('x' in this.axis || 'x1' in this.axis || 'y' in this.axis || 'y1' in this.axis) {
				var xLabelWidth = 0,
					x0LabelWidth = 0,
					yLabelWidth = 0,
					y0LabelWidth = 0;
				if ('x' in this.axis) xLabelWidth = this._getAxisLabelMaxLength('x', key, this.chart.data[0]);
				if ('x0' in this.axis) x0LabelWidth = this._getAxisLabelMaxLength('x0', key1, this.chart.data[1]);
				if ('y' in this.axis) yLabelWidth = this._getAxisLabelMaxLength('y', value, this.chart.data[0]);
				if ('y0' in this.axis) y0LabelWidth = this._getAxisLabelMaxLength('y0', value1, this.chart.data[1]);

				this.layout = {
					padding: this.styleConfig.padding,
					title: this.chart.title && this.chart.title.length ? this.styleConfig.titleHeight : 0,
					x: 'x' in this.axis ? this.styleConfig.xTitleHeight : 0,
					y: 'y' in this.axis ? this.styleConfig.yTitleWidth : 0, // title
					xLabel: xLabelWidth ? xLabelWidth + 10 : 0, // label
					yLabel: yLabelWidth ? yLabelWidth + 10 : 0,
					x0: 'x0' in this.axis ? this.styleConfig.xTitleHeight : 0,
					y0: 'y0' in this.axis ? this.styleConfig.yTitleWidth : 0,
					x0Axis: x0LabelWidth ? x0LabelWidth + 10 : 0,
					y0Axis: y0LabelWidth ? y0LabelWidth + 10 : 0,
					legend: {
						list: [], // 计算后的图例列表
						totalWidth: 0, // 图例所占容器的总宽度
						widthArr: [], // 图例极值存放没一列最长的图例长度
						columnLegendCount: 0 // 一列所放的图例个数
					},
					area: { // 画图区域
						w: 0,
						h: 0
					},
					w: this.width,
					h: this.height
				};

			} else {
				this.layout = {
					padding: this.styleConfig.padding,
					title: this.chart.title && this.chart.title.length ? this.styleConfig.titleHeight : 0,
					x: 0,
					y: 0, // title
					xLabel: 0, // label
					yLabel: 0,
					x0: 0,
					y0: 0,
					x0Axis: 0,
					y0Axis: 0,
					legend: {
						list: [], // 计算后的图例列表
						totalWidth: 0, // 图例所占容器的总宽度
						widthArr: [], // 图例极值存放没一列最长的图例长度
						columnLegendCount: 0 // 一列所放的图例个数
					},
					area: { // 画图区域
						w: 0,
						h: 0
					},
					w: this.width,
					h: this.height
				};
			}

			this.layout.area.w = this.width - this.layout.padding * 2 - this.layout.y - this.layout.legend.totalWidth - this.layout.yLabel - this.layout.y0 - this.layout.y0Axis;
			this.layout.area.h = this.height - this.layout.padding * 2 - this.layout.title - this.layout.x - this.layout.xLabel - this.layout.x0 - this.layout.x0Axis;
		}
	}

	_getAxisLabelMaxLength(axis, mapKey, data) {
		var arr = [];
		var arrDefault = [];

		if ('data' in this.axis[axis] && this.axis[axis].data.length) {
			arrDefault = this.axis[axis].data.concat();
		} else {
			arrDefault = data.map(val => {
				return val[mapKey];
			})
		}
		// 如果有自定义的formatter函数
		if (this.axis[axis].formatter) {
			arr = arrDefault.map((val, index) => {
				return this.axis[axis].formatter(val);
			})
		} else {
			arr = [...arrDefault];
		}

		arr.sort((n, m) => {
			return JSON.stringify(m).length - JSON.stringify(n).length;
		})

		var labelText = '' + arr[0];
		labelText = labelText.length > this.styleConfig.textMaxLength ? labelText.substring(0, this.styleConfig.textMaxLength + 1) + '...' : labelText;
		let oSvgy = d3.select('body').append('svg');
		let texty = oSvgy.append('text').text(labelText);
		var labelWidth = (texty.nodes())[0].getBBox().width + 10;
		oSvgy.remove();
		return labelWidth;
	}

	// ceo 图例纵向分组系数  纵向分成几组
	_computedLegendLayout(svg, categoryArr, ceo = 1) {
		var h = this.layout.area.h;
		var singleLegendH = this.styleConfig.legend.h + this.styleConfig.legend.m;
		var tmpCeo = this.layout.area.h;
		if (ceo != 1) tmpCeo = this.layout.area.h / ceo - this.styleConfig.composeChartLegendGroupMargin;
		var columnCount = Math.ceil(tmpCeo / singleLegendH);
		this.layout.legend.columnLegendCount = categoryArr.length > columnCount ? columnCount : categoryArr.length;

		// 超出一列的高度
		if (categoryArr.length > columnCount) {
			// 计算列
			var legendRowArr = [];
			var temp = [];
			var count = 0;
			var legendWidthArr = [];

			do {
				legendRowArr.push(categoryArr.slice(count * columnCount, (count + 1) * columnCount));
				temp = categoryArr.slice((count + 1) * columnCount);
				count++;
			} while (temp.length > columnCount);
			legendRowArr.push(temp);
			this.layout.legend.list = legendRowArr;
			// 计算每一行最大文字长度
			var textWidth = [];
			legendRowArr.forEach((value, index) => {
				textWidth.push([]);
				value.forEach((val, i) => {
					var testLegendText = svg.append('text').attr('class', 'test-legend-text').attr('opacity', 0)
						.text(('' + val).substring(0, this.styleConfig.legend.textMaxLength));
					var width = testLegendText.nodes()[0].getBBox().width;
					textWidth[index].push(width);
					testLegendText.remove();
				})
				textWidth[index].sort((a, b) => b - a);
				textWidth[index] = textWidth[index][0];
			})

			// 通过dom计算宽度 文字宽度
			textWidth.forEach((val, index) => {
				legendWidthArr[index] = {
					width: val,
					overflow: false
				};

				legendWidthArr[index].width = val + this.styleConfig.legend.w + this.styleConfig.legend.m + this.styleConfig.legend.p;
				this.layout.legend.totalWidth += legendWidthArr[index].width;
				this.layout.legend.widthArr = legendWidthArr.concat([]);
			})
		} else {
			// 没有超出
			var temp = categoryArr.concat([]);
			this.layout.legend.list = [categoryArr];

			temp.forEach((val, index) => {
				var testLegendText = svg.append('text').attr('class', 'test-legend-text').attr('opacity', 0)
					.text(('' + val).substring(0, this.styleConfig.legend.textMaxLength));
				temp[index] = testLegendText.nodes()[0].getBBox().width;
				testLegendText.remove();
			})
			temp.sort((a, b) => b - a);
			var obj = {
				width: 0,
				overflow: false
			}
			this.layout.legend.totalWidth = temp[0] + this.styleConfig.legend.w + this.styleConfig.legend.m + this.styleConfig.legend.p;
			obj.width = this.layout.legend.totalWidth;
			// obj.overflow = temp[0].length > (this.styleConfig.legend.textMaxLength - 1); // 一个字体的空间放...
			this.layout.legend.widthArr = [obj];
		}

		this.layout.area.w = this.layout.area.w - this.layout.legend.totalWidth - this.styleConfig.legendMargin;
		return this.layout.legend.totalWidth;
	}

	// 渐变图例的布局计算
	_computedGradietLegendLayout(svg, ticks) {
		if (this.legend.type === 'gradient') {
			var tmp = ticks.sort(function (a, b) {
				return ('' + b).length - ('' + a).length
			});
			var maxLenText = tmp[0];
			var testLegendText = svg.append('text').attr('class', 'test-legend-text').attr('opacity', 0)
				.text(('' + maxLenText).substring(0, this.styleConfig.legend.textMaxLength));
			var width = testLegendText.nodes()[0].getBBox().width;
			testLegendText.remove();

			var legendWidth = this.styleConfig.gradientLegend.w + this.styleConfig.legendMargin + width + 10
			this.layout.area.w = this.layout.area.w - legendWidth;
			this.layout.legend.totalWidth = legendWidth
		}
	}

	/**
	 * 数据选择 内部区分单选or多选
	 * d 当前数据
	 * curD3Object 当前元素的d3对象
	 * defaultColor 当前元素的默认画图颜色
	 */
	_applyChartSelect(d, curD3Object, defaultColor) {
		var selectedId = curD3Object.attr('_selectedId');
		if (selectedId || selectedId == 0) {
			curD3Object.attr('fill', defaultColor).attr('_selectedId', null);
			// multiple select default delete item
			if (this.selectedModule === 'multiple') {
				this.selectedData.forEach((val, index) => {
					if (val['_selectedId'] == selectedId) {
						this.selectedData.splice(index, 1);
					}
				})
			} else {
				// reset this selectedData
				this.selectedData.forEach((val, index) => {
					val._el.attr('fill', val._defaultColor);
				})
				this.selectedData = [];
			}
		} else {
			if (this.selectedModule === 'single' && this.selectedData.length) {
				// reset this selectedData
				this.selectedData.forEach((val, index) => {
					val._el.attr('fill', val._defaultColor).attr('_selectedId', null);
				})
			}
			// 生成id
			var id = 'selectId' + UUID.create().toString().replace(/-/g, '');
			curD3Object.attr('fill', this.styleConfig.selectedColor).attr('_selectedId', id);
			d._selectedId = id;
			d._defaultColor = defaultColor;
			d._el = curD3Object;
			this.selectedModule === 'multiple' ? this.selectedData.push(d) : this.selectedData = [d];
		}
		// callback
		this.chart.onselect && this.chart.onselect(this.selectedData);
	}

	// 设置数据选择模式
	setChartSelectModule(module) {
		this.chart.selectedModule = module;
		this.selectedModule = module;
		this.resetChartSelect();
	}

	// 重置数据选择
	resetChartSelect() {
		if (this.selectedData.length) {
			this.selectedData.forEach((val, index) => {
				val._el.attr('fill', val._defaultColor).attr('_selectedId', null);
			})
		}
		this.selectedData = [];
	}


	// 设置参数
	setOptions({
		chart,
		axis,
		legend,
		tooltip
	}) {

		this.chart = chart;
		this.axis = axis;
		this.legend = legend;
		this.tooltip = tooltip;
	}

	setXTitle(text) {
		this.axis.x.title = text;
	}

	setYTitle(text) {
		this.axis.y.title = text;
	}

	setChartTitle(text) {
		this.chart.title = text;
	}

	// 获取配置信息
	getOptions() {
		return {
			chart: this.chart,
			axis: this.axis,
			legend: this.legend,
			tooltip: this.tooltip
		};
	}

	// 获取全局配置
	getRootConfig() {
		return this.rootConfig;
	}

	// 设置颜色
	setColor(colorStr, index) {
		this.colors[index] = colorStr;
	}

	// 设置透明色
	setAColor(colorStr, index) {
		this.aColors[index] = colorStr;
	}

	getColors() {
		return this.colors;
	}

	_computedInterpolate(type) {
		// cardinal basic step natural  linear
		switch (type) {
			case 'cardinal':
				return d3.curveCardinal.tension(0.3);
			case 'step':
				return d3.curveStep;
			case 'basic':
				return d3.curveBasis;
			case "natural":
				return d3.curveNatural;
			default:
				return d3.curveLinear;
		}
	}

	// 一层浅拷贝
	_copyObj(targetObj) {
		var obj = {};
		for (var name in targetObj) {
			obj[name] = targetObj[name];
		}
		return obj;
	}

	_copyArr(target) {
		return target.concat();
	}

	_deepCopyObj(obj) {
		var result = {},
			oClass = _isClass(obj);
		for (var key in obj) {
			var copy = obj[key];
			if (_isClass(copy) == "Object") {
				result[key] = this._deepCopyObj(copy);
			} else if (_isClass(copy) == "Array") {
				result[key] = this._copyArr(copy);
			} else {
				result[key] = obj[key];
			}
		}
		return result;

		function _isClass(o) {
			if (o === null) return "Null";
			if (o === undefined) return "Undefined";
			return Object.prototype.toString.call(o).slice(8, -1);
		}
	}

}

export default Base;



